import {statuses} from './index'

const actions = {
  async GET_ITEMS({commit, state }, force=false) {
    if (state.status > statuses.LOADING && !force) return // Не грузим повторно
    commit("SET_STATUS", statuses.LOADING)
    const { data } = await this.$axios.get("/Cessions/")
    //:TODO Надо придумать что нить со временем
    data.map(item => item.date = new Date(item.date)) // Конвертирование датавремени.. надо что то придумать
    commit("SET_ITEMS", data)
    commit("SET_STATUS", statuses.READY)
  }
}

const mutations = {
  SET_STATUS(state, status) {
    state.status = status
  },
  SET_ERROR(state, error) {
    state.error = error
  },
  SET_ITEMS(state, data) {
    state.items = data
  }
}

export default {
  namespaced: true,
  state() {
    return {
      items: [],
      error: "",
      status: statuses.NOTLOADED
    }
  },
  actions,
  mutations
}
