import {statuses} from './index'

const actions = {
  async GET_ITEM({commit}) {
    commit("SET_STATUS", statuses.LOADING)
    const { data } = await new Promise((resolve) => {
      setTimeout(async () => {
        resolve({data: [
            {
              test1: 1,
              test2: 2,
              test3: 5
            },{
              test1: 2,
              test2: 11,
              test3: 4
            },{
              test1: 3,
              test2: 12,
              test3: 3
            },{
              test1: 4,
              test2: 13,
              test3: 2
            },{
              test1: 5,
              test2: 14,
              test3: 1
            }
          ]})
      }, 1000)
    })
    commit("SET_ITEMS", data)
    commit("SET_STATUS", statuses.READY)
  }
}

const mutations = {
  SET_STATUS(state, status) {
    state.status = status
  },
  SET_ERROR(state, error) {
    state.error = error
  },
  SET_ITEMS(state, data) {
    state.items = data
  }
}

export default {
  namespaced: true,
  state() {
    return {
      items: [],
      error: "",
      status: statuses.NOTLOADED
    }
  },
  actions,
  mutations
}