import {statuses} from './index'

const actions = {
  async GET_ITEMS({ commit, state }, force=false) {
    if (state.status > statuses.LOADING && !force) return // Не грузим повторно
    try {
      commit("SET_STATUS", statuses.LOADING)
      const { data } = await this.$axios.get("/Partners/")
      commit("SET_ITEMS", data)
      commit("SET_STATUS", statuses.READY)
    } catch (e) {
      commit("SET_ERROR", e.message)
      commit("SET_STATUS", statuses.ERROR)
    }
  },
  async GET_ITEM({ commit, state }, Id, force=false) {
    if (state.items.find(item => item.Id === Id).Full && !force) return // Не грузим повторно
    commit("SET_STATUS", statuses.WAIT)
    const { data } = await this.$axios.get(`/Partners/${Id}`)
    // const data = {Id, Name: "test  edit"}
    data.Full = true
    commit("SET_ITEM", data)
    commit("SET_STATUS", statuses.READY)
  },
  async CREATE_ITEM({ commit }, item) {
    commit("SET_STATUS", statuses.WAIT)
    const { data } = await this.$axios.post("/Partners/", item)
    // const data = await new Promise((resolve, reject) => {
    //   setTimeout(() => {
    //     resolve(Object.assign({}, {Id: 12}, item))
    //   }, 3000)
    // })
    commit("ADD_ITEM", data)
    commit("SET_STATUS", statuses.READY)
  },
  async SAVE_ITEM({ commit }, item) {
    commit("SET_STATUS", statuses.WAIT)
    const { data } = await this.$axios.put(`/Partners/${item.Id}`, item)
    // const data = item
    commit("SAVE_ITEM", data)
    commit("SET_STATUS", statuses.READY)
  },
  async DELETE_ITEM({ commit }, id) {
    commit("SET_STATUS", statuses.WAIT)
    await this.$axios.delete(`/Partners/${id}`)
    commit("DELETE_ITEM", id)
    commit("SET_STATUS", statuses.READY)
  },
}

const mutations = {
  SET_STATUS(state, status) {
    state.status = status
  },
  SET_ERROR(state, error) {
    state.error = error
  },
  SET_ITEMS(state, data) {
    state.items = data
  },
  SET_ITEM(state, data) {
    const item = state.items.find(item => item.Id === data.Id)
    Object.assign(item, data)
  },
  ADD_ITEM(state, data) {
    console.log('fuck')
    state.items.push(data)
  },
  SAVE_ITEM(state, data) {
    const item = state.items.find(item => item.Id === data.Id)
    Object.assign(item, data)
  },
  DELETE_ITEM() {console.log("Метод удаления не реализован!")},
}

const getters = {
  // ...
  getById: state => id => {
    console.log(id)
    return state.items.find(item => item.Id === id) || {}
  }
}

export default {
  namespaced: true,
  state() {
    return {
      items: [],
      error: "",
      status: statuses.NOTLOADED
    }
  },
  actions,
  mutations,
  getters
}
