<template>
  <v-app-bar app>
    <v-toolbar-title class="headline text-uppercase">
      <span>ХКД</span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-menu offset-y
            v-for="(menu_item, name, index) in menu"
            :key="index">
      <template v-slot:activator="{ on }">
        <v-btn
            color="primary"
            v-on="on"
            class="ma-1"
        >
          {{ name }}
        </v-btn>
      </template>
      <v-list>
        <v-list-item
            v-for="(value, name, index) in menu_item"
            :key="index"
            @click="$router.push({ name: value })"
        >
          <v-list-item-title>{{ name }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <v-btn
        color="primary"
        class="ma-1"
    >
      Выход
    </v-btn>
  </v-app-bar>
</template>
<script>
import menu from '../utils/menu'
export default {
  name: 'Toolbar',
  components: {},
  data: () => ({
    menu
  }),
};
</script>