<template>
  <v-snackbar
      v-model="snackbar"
      :timeout="time"
      :top="true"
      :color="color"
  >
    {{ text_snackbar }}
    <v-btn
        color="primary"
        @click="snackbar = false"
    >
      Закрыть
    </v-btn>
  </v-snackbar>
</template>

<script>
  export default {
    data() {
      return {
        color: "",
        snackbar: false,
        text_snackbar: "",
        rightDrawer: false,
        time: 3000
      }
    },
    mounted() {
      this.$bus.$on("sendError", (message) => { this.sendSnack(message, "error") })
      this.$bus.$on("sendInfo", (message) => { this.sendSnack(message) })
      this.$bus.$on("sendSuccess", (message) => { this.sendSnack(message, "success") })
    },
    destroyed() {
      this.$bus.$off("sendError")
      this.$bus.$off("sendInfo")
      this.$bus.$off("sendSuccess")
    },
    methods: {
      sendSnack(error, color = "info") {
        this.color = color
        this.text_snackbar = error
        this.snackbar = true
      }
    }
  }
</script>
