import Vue from 'vue'
import Vuex from 'vuex'
import dataTest from './dataTest'
import contracts from './contracts'
import partners from './partners'
import cessions from './cessions'

Vue.use(Vuex)

export const statuses = {
  NOTLOADED: 0,
  LOADING: 1,
  READY: 2,
  // CHANGED: 3, // Что то в объектах изменено
  WAIT: 4, // Статус ожидания ответа сервера, какой нить экшн
  ERROR: -1
}

export const localStatuses = {
  VIEW: 1,
  EDITOR: 2,
  CHANGED: 3,
  WAIT: 4, // Статус ожидания ответа сервера, какой нить экшн
  ERROR: -1
}

export default new Vuex.Store({
  state: {

  },
  mutations: {
    AUTH_REQUEST(state) {
      state.status = statuses.LOADING
    },
    AUTH_SUCCESS(state, token) {
      state.status = statuses.READY
      state.token = token
    },
    AUTH_ERROR(state) {
      state.status = statuses.ERROR
    }
  },
  actions: {},
  modules: {
    dataTest,
    contracts,
    partners,
    cessions
  }
})
