const mutations = {
  SET_STATUS(state, status) {
    state.status = status
  },
  SET_ERROR(state, error) {
    state.error = error
  },
  SET_ITEMS(state, data) {
    state.items = data
  }
}

export default {
  namespaced: true,
  state() {
    return {
      items: [],
      error: "",
      status: statuses.NOTLOADED
    }
  },
  actions,
  mutations
}
