<template>
  <v-dialog v-model="visible" persistent max-width="400">
    <v-card>
      <v-card-title>
        Подтвердите действие!
      </v-card-title>

      <v-card-text>
        {{ message }}
        <v-text-field v-if="prompt" v-model="response" />
      </v-card-text>
      <v-card-actions>
        <div class="flex-grow-1"></div>
        <v-btn
            v-if="alert || confirm"
            color="primary"
            text
            @click="set(true)"
        >
          OK
        </v-btn>
        <v-btn
            v-if="prompt"
            color="primary"
            text
            @click="set(response)"
        >
          OK
        </v-btn>
        <v-btn
            v-if="confirm || prompt"
            color="primary"
            text
            @click="set(false)"
        >
          Отмена
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
const types = {
  ALERT: 0,
  CONFIRM: 1,
  PROMPT: 2
}
export default {
  data: () => ({
    type: "",
    visible: false,
    message: "",
    response: "",
    resolve: ()=>{console.error("init resolve")}
  }),
  mounted() {
    this.$modal.alert = this.modal(types.ALERT)
    this.$modal.confirm = this.modal(types.CONFIRM)
    this.$modal.prompt = this.modal(types.PROMPT)
  },
  computed: {
    alert() {
      return this.type === types.ALERT
    },
    confirm() {
      return this.type === types.CONFIRM
    },
    prompt() {
      return this.type === types.PROMPT
    }
  },
  methods: {
    modal(type) {
      if(this.visible) throw new Error("Что то пошло не так. Вызов окна поверх другого окна.. Одновременно не должно быть больше одного окна.")
      return async (message) => {
        this.type = type
        this.message = message
        this.visible = true
        return await new Promise((resolve) => {
          this.resolve = resolve
        })
      }
    },
    set(result) {
      this.message = ""
      this.visible = false
      this.resolve(result)
      this.response = ""
    }
  }
}
</script>
